// VTT åœ°å›¾ URL
const VTT_MAP_URL = 'https://yyk9137.github.io/my-vtt-maps/vtt-map-viewer/index.html';

/**
 * åˆå§‹åŒ–å‡½æ•° - å¯¼å‡ºç»™é…’é¦†åŠ©æ‰‹è°ƒç”¨
 */
export function init() {
  // ç­‰å¾… DOM åŠ è½½å®Œæˆ
  if (document.readyState === 'loading') {
    document.addEventListener('DOMContentLoaded', loadVTTMap);
  } else {
    loadVTTMap();
  }
}

/**
 * åŠ è½½ VTT åœ°å›¾æ‚¬æµ®çª—
 */
function loadVTTMap() {
  // æ£€æŸ¥ jQuery æ˜¯å¦å¯ç”¨
  if (typeof $ === 'undefined') {
    console.error('âŒ jQuery æœªåŠ è½½,æ— æ³•åˆå§‹åŒ– VTT åœ°å›¾æ‚¬æµ®çª—');
    return;
  }

  // ä½¿ç”¨ jQuery çš„æ–¹å¼åˆå§‹åŒ–
  $(() => {
    toastr.success('ğŸ—ºï¸ VTT åœ°å›¾æ‚¬æµ®çª—å·²åŠ è½½');

    // åˆ›å»ºæµ®åŠ¨æŒ‰é’®
    createFloatingButton();

    // æ³¨å†Œå¿«æ·é”®
    registerKeyboardShortcuts();
  });

  // å¸è½½æ—¶æ¸…ç†
  $(window).on('pagehide', () => {
    $('#vtt-map-button').remove();
    $('#vtt-floating-window').remove();
    toastr.info('ğŸ—ºï¸ VTT åœ°å›¾æ‚¬æµ®çª—å·²å¸è½½');
  });
}

/**
 * åˆ›å»ºæµ®åŠ¨æŒ‰é’®
 */
function createFloatingButton(): void {
  // é¿å…é‡å¤åˆ›å»º
  if ($('#vtt-map-button').length > 0) {
    return;
  }

  const button = $('<button>')
    .attr('id', 'vtt-map-button')
    .attr('title', 'æ‰“å¼€ VTT åœ°å›¾ (Ctrl+M)')
    .css({
      position: 'fixed',
      bottom: '20px',
      right: '20px',
      zIndex: '9999',
      display: 'flex',
      alignItems: 'center',
      gap: '8px',
      padding: '12px 20px',
      background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
      color: 'white',
      border: 'none',
      borderRadius: '50px',
      boxShadow: '0 4px 15px rgba(102, 126, 234, 0.4)',
      fontSize: '16px',
      fontWeight: '600',
      cursor: 'pointer',
      transition: 'all 0.3s ease',
    })
    .html(`
      <svg width="24" height="24" viewBox="0 0 24 24" fill="currentColor">
        <path d="M20.5 3l-.16.03L15 5.1 9 3 3.36 4.9c-.21.07-.36.25-.36.48V20.5c0 .28.22.5.5.5l.16-.03L9 18.9l6 2.1 5.64-1.9c.21-.07.36-.25.36-.48V3.5c0-.28-.22-.5-.5-.5zM15 19l-6-2.11V5l6 2.11V19z"/>
      </svg>
      <span>åœ°å›¾</span>
    `)
    .on('click', () => {
      toggleFloatingWindow();
    })
    .on('mouseenter', function () {
      $(this).css({
        transform: 'translateY(-2px)',
        boxShadow: '0 6px 20px rgba(102, 126, 234, 0.6)',
      });
    })
    .on('mouseleave', function () {
      $(this).css({
        transform: 'translateY(0)',
        boxShadow: '0 4px 15px rgba(102, 126, 234, 0.4)',
      });
    });

  $('body').append(button);
}

/**
 * æ³¨å†Œé”®ç›˜å¿«æ·é”®
 */
function registerKeyboardShortcuts(): void {
  $(document).on('keydown', e => {
    // Ctrl + M: æ‰“å¼€/å…³é—­åœ°å›¾
    if (e.ctrlKey && e.key === 'm') {
      e.preventDefault();
      toggleFloatingWindow();
    }
  });
}

/**
 * åˆ‡æ¢æ‚¬æµ®çª—æ˜¾ç¤º/éšè—
 */
function toggleFloatingWindow(): void {
  const existingWindow = $('#vtt-floating-window');

  if (existingWindow.length > 0) {
    // å·²å­˜åœ¨,åˆ‡æ¢æ˜¾ç¤º
    if (existingWindow.is(':visible')) {
      existingWindow.hide();
    } else {
      existingWindow.show();
    }
  } else {
    // ä¸å­˜åœ¨,åˆ›å»ºæ–°çª—å£
    createFloatingWindow();
  }
}

/**
 * åˆ›å»ºæ‚¬æµ®çª—
 */
function createFloatingWindow(): void {
  // åˆ›å»ºçª—å£å®¹å™¨
  const window = $('<div>')
    .attr('id', 'vtt-floating-window')
    .css({
      position: 'fixed',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      width: '80vw',
      height: '80vh',
      maxWidth: '1400px',
      minWidth: '400px',
      minHeight: '300px',
      background: '#1a1a1a',
      border: '2px solid #667eea',
      borderRadius: '12px',
      boxShadow: '0 10px 40px rgba(0, 0, 0, 0.5)',
      zIndex: '10000',
      display: 'flex',
      flexDirection: 'column',
      overflow: 'hidden',
    });

  // åˆ›å»ºæ ‡é¢˜æ 
  const titleBar = createTitleBar(window);
  window.append(titleBar);

  // åˆ›å»ºå†…å®¹åŒºåŸŸ
  const content = createContent();
  window.append(content);

  // æ·»åŠ åˆ°é¡µé¢
  $('body').append(window);

  // å¯ç”¨æ‹–åŠ¨
  enableDragging(window, titleBar);

  // å¯ç”¨è°ƒæ•´å¤§å°
  enableResizing(window);

  // æ³¨å†Œçª—å£å¿«æ·é”®
  registerWindowShortcuts(window);

  // æ˜¾ç¤ºåŠ¨ç”»
  window.hide().fadeIn(300);
}

/**
 * åˆ›å»ºæ ‡é¢˜æ 
 */
function createTitleBar(window: JQuery): JQuery {
  const titleBar = $('<div>').css({
    padding: '12px 20px',
    background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    cursor: 'move',
    userSelect: 'none',
  });

  // æ ‡é¢˜
  const title = $('<div>')
    .css({
      display: 'flex',
      alignItems: 'center',
      gap: '10px',
      color: 'white',
      fontSize: '18px',
      fontWeight: '600',
    })
    .html(`
      <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
        <path d="M20.5 3l-.16.03L15 5.1 9 3 3.36 4.9c-.21.07-.36.25-.36.48V20.5c0 .28.22.5.5.5l.16-.03L9 18.9l6 2.1 5.64-1.9c.21-.07.36-.25.36-.48V3.5c0-.28-.22-.5-.5-.5zM15 19l-6-2.11V5l6 2.11V19z"/>
      </svg>
      <span>VTT åœ°å›¾æŸ¥çœ‹å™¨</span>
    `);

  // æ§åˆ¶æŒ‰é’®
  const controls = $('<div>').css({ display: 'flex', gap: '8px' });

  // æœ€å°åŒ–æŒ‰é’®
  const minimizeBtn = createControlButton('â–', 'æœ€å°åŒ–', '#ff9800', '#fb8c00', () => {
    const content = window.find('.vtt-content');
    if (content.is(':visible')) {
      content.hide();
      window.css('height', 'auto');
      minimizeBtn.html('â¬œ');
    } else {
      content.show();
      window.css('height', '80vh');
      minimizeBtn.html('â–');
    }
  });

  // å…¨å±æŒ‰é’®
  const fullscreenBtn = createControlButton('â›¶', 'å…¨å±', '#2196f3', '#1e88e5', () => {
    if (window.css('width') === '100vw') {
      // é€€å‡ºå…¨å±
      window.css({
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: '80vw',
        height: '80vh',
        borderRadius: '12px',
      });
      fullscreenBtn.html('â›¶');
    } else {
      // è¿›å…¥å…¨å±
      window.css({
        top: '0',
        left: '0',
        transform: 'none',
        width: '100vw',
        height: '100vh',
        borderRadius: '0',
      });
      fullscreenBtn.html('ğŸ——');
    }
  });

  // å…³é—­æŒ‰é’®
  const closeBtn = createControlButton('âŒ', 'å…³é—­ (Esc)', '#f44336', '#e53935', () => {
    window.hide();
  });

  controls.append(minimizeBtn, fullscreenBtn, closeBtn);
  titleBar.append(title, controls);

  return titleBar;
}

/**
 * åˆ›å»ºæ§åˆ¶æŒ‰é’®
 */
function createControlButton(
  text: string,
  title: string,
  bgColor: string,
  hoverColor: string,
  onClick: () => void,
): JQuery {
  return $('<button>')
    .attr('title', title)
    .css({
      padding: '6px 12px',
      border: 'none',
      borderRadius: '5px',
      color: 'white',
      fontSize: '14px',
      fontWeight: '600',
      cursor: 'pointer',
      background: bgColor,
      transition: 'all 0.2s ease',
    })
    .html(text)
    .on('click', onClick)
    .on('mouseenter', function () {
      $(this).css({ background: hoverColor, transform: 'scale(1.05)' });
    })
    .on('mouseleave', function () {
      $(this).css({ background: bgColor, transform: 'scale(1)' });
    });
}

/**
 * åˆ›å»ºå†…å®¹åŒºåŸŸ
 */
function createContent(): JQuery {
  const content = $('<div>')
    .addClass('vtt-content')
    .css({
      flex: '1',
      position: 'relative',
      overflow: 'hidden',
      background: '#0a0a0a',
    });

  // åŠ è½½æç¤º
  const loading = $('<div>').css({
    position: 'absolute',
    top: '0',
    left: '0',
    width: '100%',
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    gap: '20px',
    background: '#0a0a0a',
    color: 'white',
    zIndex: '1',
  }).html(`
    <div style="width: 50px; height: 50px; border: 4px solid rgba(102, 126, 234, 0.3); border-top-color: #667eea; border-radius: 50%; animation: spin 1s linear infinite;"></div>
    <p style="margin: 0; font-size: 18px; color: #667eea;">æ­£åœ¨åŠ è½½ VTT åœ°å›¾...</p>
  `);

  // åˆ›å»º iframe
  const iframe = $('<iframe>')
    .css({
      width: '100%',
      height: '100%',
      border: 'none',
      background: 'white',
    })
    .attr('src', VTT_MAP_URL)
    .attr('allow', 'fullscreen')
    .on('load', () => {
      loading.fadeOut();
      toastr.success('ğŸ—ºï¸ åœ°å›¾åŠ è½½å®Œæˆ');
    })
    .on('error', () => {
      loading.html(`
        <div style="font-size: 50px;">âš ï¸</div>
        <p>åœ°å›¾åŠ è½½å¤±è´¥</p>
        <small style="font-size: 14px; color: #999;">è¯·æ£€æŸ¥ URL æ˜¯å¦æ­£ç¡®</small>
      `);
      toastr.error('âŒ åœ°å›¾åŠ è½½å¤±è´¥');
    });

  content.append(loading, iframe);
  return content;
}

/**
 * å¯ç”¨æ‹–åŠ¨
 */
function enableDragging(window: JQuery, titleBar: JQuery): void {
  let isDragging = false;
  let startX = 0;
  let startY = 0;

  titleBar.on('mousedown', e => {
    // å¿½ç•¥æŒ‰é’®ç‚¹å‡»
    if ($(e.target).closest('button').length > 0) {
      return;
    }

    isDragging = true;
    const offset = window.offset()!;
    startX = e.pageX - offset.left;
    startY = e.pageY - offset.top;
    titleBar.css('cursor', 'grabbing');
    $('body').css('userSelect', 'none');
  });

  $(document).on('mousemove', e => {
    if (isDragging && window.css('width') !== '100vw') {
      const x = e.pageX - startX;
      const y = e.pageY - startY;
      const maxX = $(window).width()! - window.outerWidth()!;
      const maxY = $(window).height()! - window.outerHeight()!;

      window.css({
        left: Math.max(0, Math.min(x, maxX)) + 'px',
        top: Math.max(0, Math.min(y, maxY)) + 'px',
        transform: 'none',
      });
    }
  });

  $(document).on('mouseup', () => {
    if (isDragging) {
      isDragging = false;
      titleBar.css('cursor', 'move');
      $('body').css('userSelect', '');
    }
  });
}

/**
 * å¯ç”¨è°ƒæ•´å¤§å°
 */
function enableResizing(window: JQuery): void {
  // åˆ›å»ºè°ƒæ•´æ‰‹æŸ„
  const directions = ['n', 'e', 's', 'w', 'ne', 'se', 'sw', 'nw'];
  directions.forEach(dir => {
    const handle = $('<div>').css({
      position: 'absolute',
      zIndex: '10',
      ...getHandlePosition(dir),
      cursor: `${dir}-resize`,
    });
    window.append(handle);
  });

  let isResizing = false;
  let currentDirection = '';
  let startX = 0;
  let startY = 0;
  let startWidth = 0;
  let startHeight = 0;
  let startLeft = 0;
  let startTop = 0;

  window.find('div').each(function () {
    const handle = $(this);
    const position = handle.css('position');
    if (position === 'absolute' && handle.css('cursor').includes('resize')) {
      handle.on('mousedown', e => {
        if (window.css('width') === '100vw') return; // å…¨å±æ—¶ç¦ç”¨

        isResizing = true;
        currentDirection = handle.css('cursor').replace('-resize', '');
        startX = e.pageX;
        startY = e.pageY;
        startWidth = window.width()!;
        startHeight = window.height()!;
        startLeft = window.offset()!.left;
        startTop = window.offset()!.top;
        $('body').css('userSelect', 'none');
        e.preventDefault();
      });
    }
  });

  $(document).on('mousemove', e => {
    if (isResizing) {
      const deltaX = e.pageX - startX;
      const deltaY = e.pageY - startY;
      let newWidth = startWidth;
      let newHeight = startHeight;
      let newLeft = startLeft;
      let newTop = startTop;

      if (currentDirection.includes('e')) newWidth = startWidth + deltaX;
      if (currentDirection.includes('w')) {
        newWidth = startWidth - deltaX;
        newLeft = startLeft + deltaX;
      }
      if (currentDirection.includes('s')) newHeight = startHeight + deltaY;
      if (currentDirection.includes('n')) {
        newHeight = startHeight - deltaY;
        newTop = startTop + deltaY;
      }

      newWidth = Math.max(400, newWidth);
      newHeight = Math.max(300, newHeight);

      window.css({
        width: newWidth + 'px',
        height: newHeight + 'px',
        left: newLeft + 'px',
        top: newTop + 'px',
      });
    }
  });

  $(document).on('mouseup', () => {
    if (isResizing) {
      isResizing = false;
      $('body').css('userSelect', '');
    }
  });
}

/**
 * è·å–è°ƒæ•´æ‰‹æŸ„çš„ä½ç½®
 */
function getHandlePosition(direction: string): Record<string, string> {
  const positions: Record<string, Record<string, string>> = {
    n: { top: '0', left: '10px', right: '10px', height: '5px' },
    e: { top: '10px', right: '0', bottom: '10px', width: '5px' },
    s: { bottom: '0', left: '10px', right: '10px', height: '5px' },
    w: { top: '10px', left: '0', bottom: '10px', width: '5px' },
    ne: { top: '0', right: '0', width: '10px', height: '10px' },
    se: { bottom: '0', right: '0', width: '10px', height: '10px' },
    sw: { bottom: '0', left: '0', width: '10px', height: '10px' },
    nw: { top: '0', left: '0', width: '10px', height: '10px' },
  };
  return positions[direction] || {};
}

/**
 * æ³¨å†Œçª—å£å¿«æ·é”®
 */
function registerWindowShortcuts(window: JQuery): void {
  $(document).on('keydown', e => {
    // Esc: å…³é—­çª—å£
    if (e.key === 'Escape' && window.is(':visible')) {
      window.hide();
    }
  });
}

// æ·»åŠ æ—‹è½¬åŠ¨ç”»
const style = document.createElement('style');
style.textContent = `
  @keyframes spin {
    to { transform: rotate(360deg); }
  }
`;
document.head.appendChild(style);

// è‡ªåŠ¨åˆå§‹åŒ–
init();

// é»˜è®¤å¯¼å‡º
export default { init };

